:root {
    --br-raduis: 0.61rem;
}

.cart {
    position: relative;
    width: 12rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.front {
    width: 10.5rem;
    height: 15.8rem;
    overflow: hidden;
    z-index: 1;
}

.front img {
    object-fit: cover;
    width: 100%;
    border-radius: var(--br-raduis);
    height: 80%;
}

.info {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.51rem;
    text-transform: uppercase;
    font-size: 1.3rem;
}

.social ul {
    display: flex;
    justify-content: space-evenly;
    font-size: 1rem;
    gap: 0.1rem;
}

.social ul li {
    display: grid;
    place-content: center;
    height: 1.5em;
    width: 1.5em;
    background-color: hsl(0, 0%, 0%, 0.85);
    color: rgba(255, 255, 255);
    border: 1px solid hsl(0, 0%, 0%, 0.4);
    border-radius: 100vw;
    cursor: pointer;
}

.Cart_background {
    position: absolute;
    top: 0%;
    bottom: 0%;
    left: 0%;
    right: 0;
    opacity: 0;
    background: rgb(40, 46, 54);
    z-index: 0;
    transform: translateY(-10%) scale(0.2, 0.9);
    border-radius: var(--br-raduis);
}

.back {
    opacity: 0;
    height: 0;
    z-index: 1;
}

.cart:hover .back {
    display: flex;
    height: auto;
    flex-direction: column;
    gap: 1rem;
    transition: opacity 250ms cubic-bezier(0.21, 1, 0.81, 1);
    opacity: 1;
    font-size: 0.81rem;
    color: #fff;
    text-align: center;
}

.cart:hover .back .social ul {
    justify-content: center;
    gap: 0.51rem;
    font-size: 1.5rem;
}

.cart:hover .front {
    background: none;
}

.cart:hover .front .social {
    transform: 300ms;
    opacity: 0;
}

.cart:hover .front .info h5 {
    transition: 350ms ease;
    color: #fff;
    margin-left: 50%;
    transform: translate(-50%, 0);
}

.cart:hover .Cart_background,
.Cart_background:hover {
    transition: transform 200ms cubic-bezier(0.21, 1, 0.81, 1), opacity 250ms;
    transform: translateY(10%) scale(1.4, 1);
    opacity: 1;
}