.teamwrapper {
    min-height: 100vh;
    background-color: #fafafa;
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;
}

.back {
    position: absolute;
    width: 100vw;
    height: 50vw;
    background-color: var(--golden-color);
    clip-path: ellipse(58vw 50vh at 50% 15%);
}

.members {
    padding-top: 20vh;
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2.5rem;
    align-items: flex-start;
    padding-bottom: 5vh;
    z-index: 0;
}

.info {
    margin-bottom: 5vh;
    text-align: center;
    font-size: 0.75rem;
    padding: 0 10vw;
}